import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import styled from "styled-components";
import { privateAPI } from "../apis/user";

import FavoritesBox from "../components/mypage/FavoritesBox";
import Pagination from "../components/pagination/Pagination";
import LogoutP from "../assets/mypage/LogoutImg.svg"
import noScrapImg from "../assets/scrapflow/noScrap.png";

export default function FavoriteRecreation({ handleLogin, isLoggedIn }) {
  const [isLogoutModalOpen, setLogoutModalOpen] = useState(false);
  const navigate = useNavigate();

  const handleMyInfoClick = () => {
    navigate(`/mypage/myinfo`);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };
  const handleFavoritesClick = () => {
    navigate(`/mypage/favorites`);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const openLogoutModal = () => {
    setLogoutModalOpen(true);
  };

  const closeLogoutModal = () => {
    setLogoutModalOpen(false);
  };
  
  const handleLogout = async () => {
    try {
      const response = await privateAPI.delete("/api/auth/logout");
      localStorage.clear();
    } catch (error) {
      console.log("   : ", error);
    }
    handleLogin(false);
    setLogoutModalOpen(false);
  };

  //  
  const [datas, setDatas] = useState([]);
  //    
  const [loading, setLoading] = useState(false);
  //   
  const [currentPage, setCurrentPage] = useState(0);
  //  
  const [pages, setPages] = useState(1);

  //     
  useEffect(() => {
    const call = async () => {
      setLoading(true);
      try {
        const response = await privateAPI.get(`/api/users/me/favorites/recreations`);
        setDatas(response.data.result.recreationList);
        setPages(response.data.result.totalPages);
        setLoading(false);
      } catch (error) {
        console.log("    : ", error);
      }
    };
    call();
  });

  return (
    <Container>
    {/* */}
      <SideBar>
        <Title></Title>
        <MenuList>
            <MenuItem onClick={handleMyInfoClick}> </MenuItem>
            <MenuItem style={{backgroundColor: "#B1BEFF"}} onClick={handleFavoritesClick}>  </MenuItem>
            <MenuItem onClick={openLogoutModal}></MenuItem>
        </MenuList>
      </SideBar>

      {/* */}
      <Content>
        <RecreationWrap>
          <RecreationTitle> </RecreationTitle>
          {datas.length !== 0 ?( 
            <FavoritesParent> 
              {datas && datas.map((data) => <FavoritesBox content={data}/>)} 
            </FavoritesParent>
          ) : (
            <NoneWrap>
              <MyFlowNoneImg src={noScrapImg} />
              <NoneFavorite>  </NoneFavorite>
            </NoneWrap>
          )}
          <Pagination currentPage={currentPage} pageNum={pages} setCurrentPage={setCurrentPage}/>
        </RecreationWrap>
      </Content>

      {/* */}
      <RightSide/>

      {/* */}
      {isLogoutModalOpen && (
        <LogoutModal>
          <ModalContent>
            <ModalTitle> ?</ModalTitle>
            <SemiTitle>    .</SemiTitle>
            <LogoutImg src={LogoutP}/>
            <ModalBut>
              <LogoutButton onClick={handleLogout}></LogoutButton>
              <CloseButton onClick={closeLogoutModal}></CloseButton>
            </ModalBut>
          </ModalContent>
        </LogoutModal>
      )}
    </Container>
  );
}

const Container = styled.div`
  display: flex;
`;

// 
const SideBar = styled.div`
  width: 320px;
  height: 713px;
  box-sizing: border-box;
  font-size: 22px;
  border: solid #cacdd2 1px;
  border-bottom: none;
  border-right: none;
  color: #1b1d1f;
`;

const Title = styled.div`
  width: 260px;
  padding: 30px;
  font-size: 22px;
`;

const MenuList = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  border: solid #cacdd2 1px;
  border-left: none;
  border-right: none;
`;

const MenuItem = styled.div`
  text-align: center;
  cursor: pointer;
  padding: 20px;
  width: 280px;
  border-bottom: solid #cacdd2 1px;
`;

// 
const Content = styled.div`
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  border: solid #cacdd2 1px;
  border-bottom: none;
`;

const RecreationWrap = styled.div`
  width: 1128px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const RecreationTitle = styled.div`
  margin-top: 100px;
  font-size: 40px;
  font-weight: 600;
`;

const FavoritesParent = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 284px);
  row-gap: 20px;
  column-gap: 30px;
  margin-top: 39px;
`;

const NoneWrap  = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const MyFlowNoneImg = styled.img`
  margin-top: 150px;
  width: 150px;
  height: 150px;
`;

const NoneFavorite = styled.div`
  width: 100%;
  margin-top: 20px;
  text-align: center;
  height: 150px;
  font-size: 30px;
`;

// 
const RightSide = styled.div`
  width: 5.7325%;
  background-color: #f7f8f9;
`;


// 
const LogoutModal = styled.div`
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  z-index: 999;
`;

const ModalContent = styled.div`
  background-color: #f7f8f9;
  width: 400px;
  height: 395px;
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 230px;
`;

const ModalTitle = styled.div`
  font-size: 30px;
  font-weight: 600;
`;

const SemiTitle = styled.div`
  font-size: 17px;
  font-weight: 600;
  margin-top: 10px;
`;

const LogoutImg = styled.img`
  width: 200px;
  height: 210px;
  margin-top: 10px;
  margin-bottom: 20px;
`;

const ModalBut = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  border-top: solid gray 1px;
  width: 440px;
`;

const LogoutButton = styled.div`
  display: flex;
  justify-content: center;
  border: solid black 1px;
  padding: 12px 10px;
  border-radius: 30px;
  width: 110px;
  font-weight: 600;
  cursor: pointer;
`;

const CloseButton = styled(LogoutButton)`
  background-color: #4036ed;
  color: white;
  border: none;
  margin-left: 80px;
`;
