import React, { useState, useEffect } from "react";
import { publicAPI, privateAPI } from "../apis/user";
import styled from "styled-components";
import penguinImg from "../assets/myflow/penguin.png";
import noFlowImg from "../assets/myflow/noFlow.png";
import MadeFlowBox from "../components/flow/MadeFlowBox";
import { useNavigate } from "react-router-dom";
import Pagination from "../components/pagination/Pagination";

export default function MyFlow() {
  const navigate = useNavigate();
  const moveToWatch = () => {
    if (localStorage.getItem("accessToken")) navigate(`/flow/watch`);
    else alert("  .");
  };
  const moveToScrap = () => {
    if (localStorage.getItem("accessToken")) navigate(`/flow/scrap`);
    else alert("  .");
  };
  const moveToMakeFlow = () => {
    if (localStorage.getItem("accessToken")) navigate(`/flow/write`);
    else alert("  .");
  };

  //  
  const [datas, setDatas] = useState([]);
  //    
  const [loading, setLoading] = useState(false);
  //   
  const [currentPage, setCurrentPage] = useState(0);
  //   
  const [pages, setPages] = useState(1);
  //     
  const [doDel, setDoDel] = useState(false);

  //     
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      if (localStorage.getItem("accessToken")) {
        const response = await privateAPI.get(`/api/users/me/flows?page=${currentPage}`);
        console.log(" :", response);
        setDatas(response.data.result.flowList);
        setPages(response.data.result.totalPages);
      } else {
        const response = await publicAPI.get(`/api/users/me/flows?page=${currentPage}`);
        setDatas(response.data.result.flowList);
        setPages(response.data.result.totalPages);
      }
      setLoading(false);
    };
    fetchData();
    setDoDel(false);
  }, [currentPage, doDel]);

  useEffect(() => {
    console.log(datas);
  }, [datas]);

  return (
    <MyFlowWrap>
      {/*    */}
      <MyFlowMenuContainer>
        <MyFlowMenuTitle></MyFlowMenuTitle>
        <MyFlowMenuBox onClick={moveToWatch}> </MyFlowMenuBox>
        <MyFlowMenuBox style={{ backgroundColor: "#B1BEFF" }}>  </MyFlowMenuBox>
        <MyFlowMenuBox onClick={moveToScrap}>  </MyFlowMenuBox>
      </MyFlowMenuContainer>

      <MyFlowContainer>
        <div>
          {/*    - Title */}
          <MyFlowBoxContainer>
            <MyFlowBoxImage src={penguinImg} />
            <TitleBox>
              <MyFlowBoxTitle onClick={moveToMakeFlow}> </MyFlowBoxTitle>
            </TitleBox>
          </MyFlowBoxContainer>
          {/*    - Grid */}
          {datas.length !== 0 ? (
            <div>
              <MyFlowBoxParent>{datas && <MadeFlowBox datas={datas} setDoDel={setDoDel} />}</MyFlowBoxParent>
              {/*  */}
              <Pagination currentPage={currentPage} pageNum={pages} setCurrentPage={setCurrentPage} />
            </div>
          ) : (
            <MyFlowNoneBox>
              <MyFlowNoneImg src={noFlowImg} />
              <MyFlowNoneDetail>
                <div style={{ fontSize: "24px", fontWeight: "bold" }}>   !</div>
                <div style={{ fontSize: "20px", marginTop: "8px" }}>
                        .
                </div>
              </MyFlowNoneDetail>
            </MyFlowNoneBox>
          )}
        </div>
      </MyFlowContainer>
      <RightSide />
    </MyFlowWrap>
  );
}

const MyFlowWrap = styled.div`
  display: flex;
  background-color: #f7f8f9;
`;

const RightSide = styled.div`
  width: 5.7325%;
  background-color: #f7f8f9;
`;

// -  > ~ MyFlowMenuBox
const MyFlowMenuContainer = styled.div`
  background-color: white;
  border: 0.5px solid #cacdd2;
  border-bottom: 0;
  width: 320px;
  font-size: 24px;
`;

const MyFlowMenuTitle = styled.div`
  padding: 2.5vh 0vh 2.5vh 3vh;
`;

const MyFlowMenuBox = styled.div`
  border: 0.5px solid #cacdd2;
  border-right: 0px;
  border-left: 0px;
  text-align: center;
  justify-content: center;
  padding: 1.5vh 3vh;
  cursor: pointer;
`;

const MyFlowContainer = styled.div`
  background-color: white;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  border-right: 0.5px solid #cacdd2;
`;

//     - Title
const MyFlowBoxContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 30px;
`;

const MyFlowBoxImage = styled.img`
  width: 199.65px;
  height: 199.65px;
  position: relative;
  z-index: 2;
`;

const TitleBox = styled.div`
  background-color: #19297c;
  border-radius: 15vh;
  width: 527px;
  height: 109px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  position: relative;
  left: -23.65px;
  cursor: pointer;

  &:hover {
    background-color: #4036ed;
    transition: 0.2s;
  }
`;

const MyFlowBoxTitle = styled.div`
  color: white;
  width: 343px;
  height: 57px;
  font-weight: bold;
  font-size: 47px;
`;

//   - Grid
const MyFlowBoxParent = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 370px);
  row-gap: 20px;
  column-gap: 120px;
  margin-top: 39px;
`;

//     - None
const MyFlowNoneBox = styled.div`
  width: 100%;
  text-align: center;
  margin-bottom: 237px;
`;

const MyFlowNoneImg = styled.img`
  width: 120px;
  height: 120px;
`;

const MyFlowNoneDetail = styled.div`
  width: 100%;
  margin-top: 40px;
  text-align: center;
`;
