import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import fix from "../../assets/flowwrite/fix_flow_write.png";

export default function AddRecreationInfo({
  num,
  title,
  playTime,
  keywordList,
  onDelete,
}) {
  // const [title, setTitle] = useState("");
  const [time, setTime] = useState(10);

  const DetailMappings = {
    COOPERATIVE: "",
    QUICKNESS: "",
    SENSIBLE: "",
    BRAIN: "",
    CREATIVE: "",
    ACTIVE: "",
    PSYCHOLOGICAL: "",
    LUCK: "",
    COMMON_SENSE: "",
    PREPARATION: "",
  };

  useEffect(() => {
    // API     
    const fetchRecreationData = async () => {
      try {
        // API   
        const response = await axios.get(
          "https://dev.avab.shop/api/recreations/recommended",
          {
            params: {
              playTime: time,
              purpose: "SPORTS_DAY",
            },
          }
        );
        console.log("API  :", response.data);
        //    
        const { title, keywordList, playTime } = response.data;
        console.log("  :", {
          title,
          keywordList,
          playTime,
        });

        //   
        return { title, keywordList, playTime };
      } catch (error) {
        //     
        console.error("   :", error);
      }
    };

    // fetchRecreationData  
    const fetchData = async () => {
      const data = await fetchRecreationData();
      console.log(":", data); //   
    };

    // fetchData  
    fetchData();
    fetchRecreationData();
  }, [time]);

  const handleDeleteClick = () => {
    // onDelete    InfoBox .
    onDelete(num);
  };

  return (
    <div
      style={{
        display: "flex",
        gap: "8px",
        alignItems: "end",
        marginBottom: "8px",
      }}
    >
      <Line time={playTime}></Line>
      <InfoBox time={playTime}>
        {/*   */}
        <RecreationTitle>
          <Number>{num}</Number>
          <RecreationTitleInput
            type="text"
            value={title}
            style={{
              fontSize: "20px",
              fontStyle: "normal",
              fontWeight: "700",
              border: "none",
              outline: "none",
            }}
          />
          <img
            src={fix}
            alt="Fix"
            style={{ width: "24px", height: "24px", cursor: "pointer" }}
            onClick={handleDeleteClick}
          />
        </RecreationTitle>

        <KeywordBox>
          <div style={{ width: "90%", display: "flex" }}>
            {keywordList.map((keyword, index) => (
              <StyledKeyword
                key={index}
                style={{
                  padding: "5px 20px",
                  width: "123px",
                  height: "29px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  backgroundColor: "#F0F0F0",
                  borderRadius: "4px",
                  marginRight: "8px",
                }}
              >
                {DetailMappings[keyword]}
              </StyledKeyword>
            ))}
          </div>
        </KeywordBox>
        {/*    */}
        <PlayTime>
          <div
            style={{
              fontSize: "16px",
              fontStyle: "normal",
              fontWeight: "500",
              color: "#1B1D1F",
            }}
          >
            
          </div>
          <div
            style={{
              fontSize: "16px",
              fontStyle: "normal",
              fontWeight: "600",
              color: "#1B1D1F",
            }}
          >
            {playTime} 
          </div>
        </PlayTime>
      </InfoBox>
    </div>
  );
}

const Line = styled.div`
  width: 0px;
  height: ${(props) => `${Math.max((props.time / 10) * 119.004, 119.004)}px`};
  border: 5px solid #b1beff;
  border-radius: 20px;
  margin-right: 21px;

  /*transition: height 0.5s cubic-bezier(0.4, 0, 0.2, 1);*/
  // 
`;

const InfoBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  top: ${(props) =>
    props.time === 10 ? "0" : `calc(-${props.time / 10 - 1} * 119.04px)`};
  min-height: 119.004px;
`;

const RecreationTitle = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 12px;
`;

const RecreationTitleInput = styled.input`
  &::placeholder {
    color: #9fa4a9;
  }

  &:focus::placeholder {
    color: transparent;
  }
`;

const Number = styled.div`
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b1beff;
  border-radius: 50%;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  margin-right: 8px;
`;

const KeywordBox = styled.div`
  width: 371px;
  color: #9fa4a9;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  margin-bottom: 15px;
  box-sizing: border-box;
`;

const KeywordInput = styled.input`
  width: 90%;
  height: 18px;
  margin-left: 8px;
  border: none;
  outline: none;
  font-size: 16px;

  &::placeholder {
    color: #9fa4a9;
  }

  &:focus::placeholder {
    color: transparent;
  }
`;

const StyledKeyword = styled.span`
  display: flex;
  height: 25px;
  padding: 2px 10px;
  box-sizing: border-box;
  border-radius: 20px;
  background: #d9d9d9;
  font-size: 16px;
  color: #1b1d1f;
  margin-left: 8px;
  align-items: center;

  img {
    margin-left: 12px;
  }
`;

// const KeywordBox = styled.div`
//   display: flex;
//   align-items: flex-start;
//   gap: 17px;
//   margin-bottom: 21px;
// `;

